import React, { useEffect, useState } from "react";
import "./nav.css";

const Nav = () => {

  const [ pikachu, getPiKachu ] = useState([]);

  useEffect(() => {
    getDatos();
  }, []);

  const getDatos = async () => {
    const response = await fetch(`https://pokeapi.co/api/v2/pokemon/83/`);
    console.log(response);
    const datos = await response.json();
    console.log(datos)
    getPiKachu([datos])
  }
//sprites.back_default
  return(
  <div>
    <nav>
      {
        pikachu.map(item => {
          console.log("henry")
          console.log(item.sprites.front_default)
          return <img src={item.sprites.front_default} key={item.id} alt="yurien"/>
        })
      }
    
      <ul>
        <li>hiran</li>
        <li>henry</li>
        <li>rudys</li>
        <li>hiran</li>
        <li>henry</li>
        <li>rudys</li>
      </ul>
      <div>
        <span className="pepe">ruthuerhtu</span>
        <span>hgurh</span>
        <span>guhguirt</span>
        <span>drhghiu</span>
      </div>
    </nav>
  </div>
  )
}

export default Nav;